$color_1: rgb(50,50,50);
$color_2: rgb(250,80,80);
$font-family_1: 'Open Sans', sans-serif;
$border-right-color_1: rgb(248,248,248);
$border-bottom-color_1: rgb(255,255,255);
$border-left-color_1: rgb(248,248,248);

